.slider {
    position: relative;
    width: 320px;
    margin: 0 auto;
    height: 500px;
    overflow: hidden;
    white-space: nowrap;
    &__container {
        position: relative;
        height: 100%;
        width: 100%;
    }
    &__photo {
        display: inline-block;
        height: 100%;
        width: 100%;
        border-radius: 15px;
        position: relative;
        text-align: center;
    }
    &__text {
        position: absolute;
        width: 100%;
        bottom: 10%;
        text-shadow: 0 0 8px white;
    }
    &__category {
        position: absolute;
        width: 100%;
        bottom: 0;
        text-shadow: 0 0 8px white;
    }
    &__delete {
        position: absolute;
        right: 32px;
        top: 32px;
        width: 32px;
        height: 32px;
        opacity: 0.3;
        cursor: pointer;
        &:hover {
            opacity: 1;
        }
        &:before, &:after {
            position: absolute;
            left: 15px;
            content: ' ';
            height: 33px;
            width: 2px;
            background-color: #333;
        }
        &:before {
            transform: rotate(45deg);
        }
        &:after {
            transform: rotate(-45deg);
        }
    }
    &__arrow {
        position: absolute;
        width: 20px;
        height: 20px;
        border-color: transparent #f82949 #f82949 transparent;
        border-style: solid;
        border-width: 4px;
        margin-top: -4px;
        -webkit-transition: all .08s ease-in-out;
        transition: all .08s ease-in-out;
        cursor: pointer;
        &-right {
            position: relative;
            bottom: 275px;
            left: 270px;
            transform: rotate(-45deg);
        }
        &-left {
            position: relative;
            bottom: 250px;
            left: 20px;
            transform: rotate(135deg);
        }
    }
}

@media (min-width: 500px) {
    .slider {
        width: 500px;
        &__arrow {
            &-right {
                left: 450px;
            }
        }
    }
}